	import BASEURL from "../../common/baseInfo.js";
	const duration = 2000
	export default {
		components: {
			mSearch
		},
		data() {
			return {
				defaultKeyword:'请输入关键字',
				background: ['color1', 'color2', 'color3'],
				indicatorDots: true,
				autoplay: true,
				interval: 2000,
				requestOptions: {
					duration: 500,
					res: '',
				},
				personList:[]
// 				val0: '',
// 				val1: '',
// 				val2: '',
// 				val3: ''
			}
		},
		onShow() {
			this.checkLogin();
			this.sendRequest();
		},
		onLoad() {
			// this.sendRequest();
		},
		methods: {
			sendRequest(e) {
				let requestUrl = `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findAllUser.action`;
				if (e) {
					// requestUrl = `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findAllUser.action?keyword=${e}`
					requestUrl = `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findUserIfoByString.action?string=${e}`;
				}
				uni.showLoading({
					title: '加载中'
				});
				uni.request({
					url: requestUrl,
					dataType: 'text',
					data: {
						// noncestr: Date.now()
					},
					success: (res) => {
						console.log('request success', JSON.parse(res.data));
						let data = JSON.parse(res.data);
						if (data.data.length == 0) {
							uni.showToast({
								title: '没有找的相关信息',
								icon: 'none',
								mask: false,
								duration: 1500
							});
						}
						this.res = '请求结果 : ' + JSON.stringify(res);
						this.personList = data.data;
						console.log(this.personList);
					},
					fail: (err) => {
						console.log('request fail', err);
						uni.showModal({
							content: err.errMsg,
							showCancel: false
						});
					},
					complete: () => {
						uni.hideLoading()
					}
				});
			},
			openDetail(id) {
				uni.navigateTo({
					url: '../indexInvestDetail/indexInvestDetail?id='+id
				});
			},
			search(e, val) {
				this.sendRequest(e);
			},
			checkLogin() {
				let that = this;
				uni.checkSession({
					success(res) {
						console.log(res);
						uni.login({
							provider: 'weixin',
							success: res => {
								uni.request({
									url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/wxLogin?code=${res.code}`,
									method: 'POST',
									data: {},
									success: res => {
										let openId = res.data.data.openid;
										console.log(res,'succ');
										uni.setStorage({
											key: 'openIdInfo',
											data: res.data.data.openid,
											success: function () {
												console.log('success');
											}
										});
										// 获取用户信息
										uni.getUserInfo({
										  provider: 'weixin',
										  success: function (infoRes) {
											// that.login = true;
											uni.request({
												url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/updateUserByOpenId.action`,
												method: 'GET',
												data: {
													nickName: infoRes.userInfo.nickName,
													imgUrl: infoRes.userInfo.avatarUrl,
													openId:openId
												},
												success: res => {
													console.log(res);
												},
												fail: () => {},
												complete: () => {}
											});
											uni.setStorage({
												key: 'userInfo',
												data: infoRes.userInfo,
												success: function () {
													console.log('success');
													// uni.navigateBack();
												}
											});
											// console.log(infoRes);
										  }
										});
									},
									fail: (err) => {
										
											console.log(err,'err');
									},
									complete: (data) => {
											console.log(data,'data');}
								});
							},
							fail: () => {},
							complete: () => {}
						});
						// that.getUserInfoFn()
					},
					fail(err) {
						console.log(err,'err');
						uni.navigateTo({
							url: '../login/login'
						});
					}
				})
			},
			getUserInfoFn () {
				let that = this;
				uni.getUserInfo({
				  provider: 'weixin',
				  success: function (infoRes) {
					console.log(infoRes.userInfo);
					uni.setStorage({
						key: 'userInfo',
						data: infoRes.userInfo,
						success: function () {
							console.log('success');
						}
					});
				  }
				});
				uni.request({
					url: '',
					method: 'GET',
					data: {},
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
// 			 search(e, val) {
// 				console.log(e, val);
// 				this['val'+val] = e;
// 			}
		}
	}
